@import "variables";
@import "functions";
@import "extends";
@import "mixins";

/*main*/
.main{
	padding-top: 100px;
}

/*full-page*/
.full-page{
	@extend %fwh;
	padding-top: 100px;
	overflow: hidden;
	&_middle{
		display: flex;
		align-items: center;
		&__inner{
			width: 100%;
		}
	}
}

/*section*/
.section{
	padding-top: 30px;
	padding-bottom: 30px;
}

/*list*/
.list{
	list-style: none;
	li{
		padding-left: 30px;
		margin-top: 30px;
		@include fch(t);
		position: relative;
		&:before{
			@extend %ab;
			position: absolute;
		}
	}
}
ul.list{
	li{
		&:before{
			width: 9px;
			height: 9px;
			left: 3px;
			top: 6px;
			background: $rd;
		}
	}
}
ol.list{
	counter-reset: listCounter;
	li{
		&:before{
			counter-increment: listCounter;
			content: counter(listCounter)".";
			color: $be;
			font-weight: 700;
			top: 0;
			left: 0;
		}
	}
}

/*image*/
.fullwidth-img{
	width: 100%;
	height: auto;
}

/* title */
.title{
	font-size: 30px;
	font-weight: 700;
	line-height: 1.2;
	margin-bottom: em(35,30);
}

/* article */
.article{
	h2{
		font-size: 22px;
		font-weight: 700;
		line-height: 1.2;
		margin: 45px 0 30px;
		@include fch(t);
	}
	p{
		line-height: 1.35;
		margin-top: 25px;
		@include fch(t);
	}
	blockquote{
		line-height: 1.35;
		margin-top: 35px;
		@include fch(t);
	}
}